<template>
  <div class="ShipmentListPrint pa-3">
    <v-row>
      <v-col cols="4">
        <!-- <h2>{{ cData.company_name }}</h2> -->
        <v-img :src="require('@/assets/herfa-logo-01.png')" aspect-ratio="3.5" contain></v-img>
      </v-col>
      <v-col cols="4" class="d-flex justify-center align-center">
        <div class="text-center">
          <div class="text-h3 font-weight-bold">應收帳款統計表</div> 
          <h2>({{list.startDay}} ~ {{list.endDay}})</h2>
        </div>
      </v-col>
      <v-col cols="4" class="text-right">
        <h3>台南市仁德區新田二街42號之3</h3>
        <h3>TEL:(06)2794315．2794316</h3>
        <h3>FAX:(06)2794078</h3>
      </v-col>
    </v-row>

    <v-divider class="my-2"></v-divider>

    <v-simple-table>
      <template v-slot:default>
        <thead class="text-no-wrap">
          <tr class="grey lighten-4">
            <!-- <th class="d-print-none">CHECK</th> -->
            <th class="text-center text-h6 font-weight-bold d-print-none">
              <!-- <v-icon @click.stop="printCustomerList(filterShipment)">mdi-printer</v-icon> -->
              客戶統編</th>
            <th class="text-center text-h6 font-weight-bold">客戶名稱</th>
            <th class="text-center text-h6 font-weight-bold">買賣金額</th>
            <th class="text-center text-h6 font-weight-bold">沖鎖金額</th>
            <th class="text-center text-h6 font-weight-bold">折工金額</th>
            <th class="text-center text-h6 font-weight-bold">加工金額</th>
            <th class="text-center text-h6 font-weight-bold">出貨總金額</th>
          </tr>
        </thead>
        <tbody class="text-right">
          <tr v-for="(item, index) in filterShipment" :key="index">
            <!-- <td class="d-print-none"><v-checkbox></v-checkbox></td> -->
            <td class="text-center text-h6 d-print-none">
              <!-- <v-icon @click.stop="printCustomerListSingle(item)">mdi-printer</v-icon> -->
              {{item.code}}
            </td>
            <td class="text-center text-h6">{{item.name}}</td>
            <td class="text-center text-h6">{{item.allTotalPrice}}</td>
            <td class="text-center text-h6">{{item.allPunchlockPrice}}</td>
            <td class="text-center text-h6">{{item.allFoldPrice}}</td>
            <td class="text-center text-h6">{{item.allMachiningPrice}}</td>

            <td class="text-center text-h6">{{item.allTotalPrice + item.allPunchlockPrice + item.allFoldPrice + item.allMachiningPrice}}</td>

          </tr>  
            

          <tr class="grey lighten-4">
            <!-- <td class="d-print-none"></td> -->
            <!-- <td class="d-print-none"></td> -->
            <td class="text-h6 font-weight-bold text-center">各欄位小計</td>
            <td></td>
            <td class="text-h6 font-weight-bold text-center">{{ comTotal(filterShipment,'allTotalPrice') | filtermoney }}</td>
            <td class="text-h6 font-weight-bold text-center">{{ comTotal(filterShipment,'allPunchlockPrice') | filtermoney }}</td>
            <td class="text-h6 font-weight-bold text-center">{{ comTotal(filterShipment,'allFoldPrice') | filtermoney }}</td>
            <td class="text-h6 font-weight-bold text-center">{{ comTotal(filterShipment,'allMachiningPrice') | filtermoney }}</td>
            <td class="text-h6 font-weight-bold text-center">
              {{ 
                comTotal(filterShipment,'allTotalPrice')
              + comTotal(filterShipment,'allPunchlockPrice')
              + comTotal(filterShipment,'allFoldPrice')
              + comTotal(filterShipment,'allMachiningPrice')
              | filtermoney 
              }}
            </td>
          </tr>


        </tbody>

      </template>
    </v-simple-table>

    <!-- <pre>{{filterShipment}}</pre> -->
    <!-- <pre>{{items[0]}}</pre> -->

  </div>
</template>

<script>
import dayjs from "dayjs";
import isBetween from "dayjs/plugin/isBetween";
dayjs.extend(isBetween);

export default {
  data() {
    return {
      items:[],
      list: JSON.parse(this.$route.query.data),
      cData: JSON.parse(sessionStorage.getItem("cData")),
      // customerItems: [], //暫存所有客戶名單
    };
  },

  filters: {
    filtermoney(num) {
      if (!num){return ''}
      const n = Number(num);
      return `$${n.toFixed(0).replace(/./g, (c, i, a) => {
        const currency =
          i && c !== "." && (a.length - i) % 3 === 0
            ? `, ${c}`.replace(/\s/g, "")
            : c;
        return currency;
      })}`;
    },
  },

  computed: {
    //建構列表表格內容
    filterShipment() {
      //1.取得.符合時間區段的資料
      let match = this.items.filter(
        (i) => dayjs(i.date).isBetween(this.list.startDay, dayjs(this.list.endDay), null, '[]') 
      );
      //計算出1.的資料中的各項資料總合
      match = match.map(i=>{
        return {
          ...i,
          totalPrice : this.priceTotal(i),  //買賣金額
          punchlockPrice : this.optionTotal(i,'punchlock','extraPunchlockPrice'),  //沖鎖
          foldPrice : this.optionTotal(i,'fold','extraFoldPrice'),  //折工
          machiningPrice : this.optionTotal(i,'machining','extraMachiningPrice'),  //加工
        }
      })

      //2.取得所有符合資料的顧客snkey -> 取得不重覆陣列值
      let snkeyItems = match.map(i=>i.customer.snkey)  //取得所有符合資料的顧客snkey
      snkeyItems = [...new Set(snkeyItems)]  //取得不重覆陣列值

      let createTableDatas = snkeyItems.map(snkey=>{
        let matchArr = match.filter(i=>i.customer.snkey == snkey)
        
        return {
          code : matchArr[0].customer.code,
          name : matchArr[0].customer.name,
          allTotalPrice : this.comTotal(matchArr,'totalPrice'),
          allPunchlockPrice : this.comTotal(matchArr,'punchlockPrice'),
          allFoldPrice : this.comTotal(matchArr,'foldPrice'),
          allMachiningPrice : this.comTotal(matchArr,'machiningPrice'),
          data: matchArr,
        }
      })

      return createTableDatas
    },

  },

  mounted() {
    this.getAllData()
  },

  methods:{
    //取得所有資料
    getAllData() {
      this.myAxios(
        "general/getAll/" + this.$store.state.databaseName + "/shipment"
      ).then((rs) => {
        this.items = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
    //回傳單筆資料總額
    priceTotal(items) {
      return items.tempItems.reduce((total, item) => {
        return (total += this.filterFinalPrice(item));
      }, 0);
    },
    filterFinalPrice(item) {
      if (item.noSize){
        var val = item.quantity * (parseFloat(item.rawmaterial.price) + parseFloat(item.extraRawmaterialPrice));
      }else{
        var val = item.weight * (parseFloat(item.rawmaterial.price) + parseFloat(item.extraRawmaterialPrice));
      }
      return Math.round(val);  //2021.3.19 改回傳四捨五入,取整數
      // return this.roundUp(val, 0);
    },
    //各種工作總金額
    optionTotal(items,type,extraType) {
      return items.tempItems.reduce((total, item) => {
        if (item[type]){
          return (total += (parseFloat(item[type].price) + parseFloat(item[extraType])) * item.quantity);
        }else{
          return total;
        }
      }, 0);
    },
    //回傳指定項目的總合
    comTotal(items,type){
      return items.reduce((total, i) => {
        return (total += parseInt(i[type]));
      }, 0);
    },
    //列印指定客戶應收帳款列表
    printCustomerList(items){
      let data = {
        items: items,
        dates: this.list,
      }
      let routeData = this.$router.resolve({path: "/print/customershipmentlistprint",query:{data:JSON.stringify(data)}});
      window.open(routeData.href, "_blank");
    },
     //列印指定客戶應收帳款列表(單筆)
    printCustomerListSingle(item){
      let singleItem = [];
      singleItem[0] = item;
      let data = {
        items: singleItem,
        dates: this.list,
      }
      let routeData = this.$router.resolve({path: "/print/customershipmentlistprint",query:{data:JSON.stringify(data)}});
      window.open(routeData.href, "_blank");
    },
  }
};
</script>

<style>

@media print {
  @page {
    size: A4 portrait;
    margin: 0cm;
    padding: 0cm;
  }
}
</style>